import React from "react";
import { Navigate } from "react-router";
import client from "./CLASSES/client";
import getParam from "./Functions/getParam";
import Pure from "./Pure";
import Building from "./CLASSES/buildings";
import "./Building.scss";

class BuildingView extends Pure {
    state = { ready: false }
    building = null;

    onReady() {
        client
            .building(this.id)
            .then(response => {
                if (!this.componentIsReady) return;
                this.building = Building.New(response.id, client.activeVillageId, this.id, response.level);
                this.setState({ ready: true });
            });
    }
    id = getParam("id", 0, true);
    valid = isNaN(this.id) ? false : (this.id > 0 && this.id < 41);
    render() {
        if (!this.valid)
            return <Navigate to="/Village" />;
        if (!this.state.ready)
            return <></>;
        return this.building.renderBuilding();
    }
}

export default BuildingView;